@import url(https://fonts.googleapis.com/css2?family=Sen:wght@400;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Oswald:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Secular+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Overpass:wght@700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button-container{border:1px solid #27526c;border-radius:5px;color:#27526c;padding:0.2em}.button-container:hover{background-color:#27526c;color:#ffffff}

body{font-family:"Montserrat", sans-serif}p{font-size:calc(12px + 0.3vw)}h1,h2,h3,h4,h5{font-family:"Secular One", sans-serif}Nav{font-family:"Secular One", sans-serif;font-size:1.25vw}

.container{position:relative}.overlay{position:absolute;top:0;bottom:0;left:0;right:0;height:100%;width:100%;opacity:0;transition:0.4s ease;background-color:#ffffff;padding:1vw}.container:hover .overlay{opacity:1}

h3,h5{color:#27526c;font-weight:bold !important}

#knockout-text{-webkit-mask:url(/static/media/jenny.4e81b9ee.svg);mask:url(/static/media/jenny.4e81b9ee.svg);-webkit-mask-image:url(/static/media/jenny.4e81b9ee.svg)}

